/* Normalizing the CSS and creating the over all layout for the application */
* {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

body {
  margin: 7rem 5.5rem 0 5.5rem;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
}

/* Main Layout */
main {
  display: grid;
  row-gap: 4.5rem;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr 0.1fr repeat(3, 1.5fr) 0.2fr;
  grid-template-areas:
    "hero hero nav"
    "line-break line-break line-break"
    "project project project"
    "project-1 project-1 project-1"
    "project-2 project-2 project-2"
    "footer footer footer";
}

header {
  align-self: flex-start;
  grid-area: hero;
}

.linebreak {
  align-self: center;
  grid-area: line-break;
}

nav {
  grid-area: nav;
}

.project {
  grid-area: project;
}

.project-1 {
  grid-area: project-1;
}

.project-2 {
  grid-area: project-2;
}

footer {
  grid-area: footer;
}
